import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Layout/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path:'/auth',
    component:()=>import('@/views/Pages/Login/Index.vue'),
    children:[
      {
        path:'login',
        name:'Login',
        component:()=>import('@/views/Pages/Login/login.vue')
      },
      {
        path:'register',
        name:'Register',
        component:()=>import('@/views/Pages/Login/register.vue')
      }
    ]
  },
  {
    path:'/',
    redirect:'/auth/login'
  },
  {
    path: '/index',
    name: 'Home',
    component: Home,
    children:[
      {
        path: 'user',
        name: 'user',
        meta:{ title:'数据模型'},
        component: () => import('@/views/Pages/User/Index.vue')
      },
      {
        path:'admin',
        name:'admin',
        component: () => import('@/views/Pages/Admin/Index.vue')
      },
      {
        path:'adduser',
        name:'adduser',
        component: () => import('@/views/Pages/Admin/Add.vue')
      },
      {
        path:'government',
        name:'gov',
        component: () => import('@/views/Pages/Gov/Index.vue')
      },
      {
        path:'govapply',
        name:'govapply',
        component:()=>import('@/views/Pages/Gov/Apply.vue')
      },
      {
        path:'langchao',
        name:'lchao',
        component: () => import('@/views/Pages/LChao/Index.vue')
      }
    ]
  },
  {
    path:'*',
    name:'404',
    component:()=>import('@/views/Pages/404.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
