import axios from './axios'
import base from './base'
import QS from 'qs'

let apis={}

//用户登录
apis.login=(params)=>{ return axios.post(`${base.URL}/auth/login`,QS.stringify(params))};
//用户注册
apis.regsiter=(params)=>{ return axios.post(`${base.URL}/auth/register`,QS.stringify(params))};
// 获取邮箱验证码
apis.getemcode=(params)=>{ return axios.post(`${ base.URL}/auth/code`,QS.stringify(params))}; 
//验证用户是否已存在
apis.checkuser=(params)=>{return axios.get(`${base.URL}/auth/check/${params}`)}

//用户平台
//用户信息
apis.userinfo=(params)=>{ return axios.get(`${base.URL}/test/sayHello`,{params:params})};
//用户获取数据加密密钥的公钥
apis.getkey=(params)=>{ return axios.get(`${base.URL}/user/key/`,{params:params})}
//用户获取数据
apis.getdata=(params1,params2)=>{ return axios.post(`${base.URL}/user/${params1}/applyData/${params2}`)};
//用户申请使用数据
apis.usedata=(params)=>{ return axios.post(`${base.URL}${params}`)}
//用户获取可以使用的计算模型
apis.useral=(params)=>{ return axios.get(`${base.URL}/user/al`,{ params:params})};
//计算
apis.comptest=(params1,params)=>{ return axios.get(`${base.URL}${params1}`,{params:params})}

//平台管理员查看平台数据使用记录
apis.admrelist=(params)=>{ return axios.get(`${base.URL}/admin/record/list`,{ params:params})}
//管理员后台添加用户
apis.admuser=(params)=>{ return axios.post(`${base.URL}/admin/user`,QS.stringify(params))};
//管理员后台更新用户
apis.putadmuser=(params)=>{ return axios.put(`${base.URL}/admin/user`,QS.stringify(params))};
//管理员后台删除用户
apis.deladmuser=(params)=>{ return axios.delete(`${ base.URL}/admin/user/${params}`)};
//平台管理员查看平台用户注册信息
apis.admuserlist=(params)=>{ return axios.get(`${base.URL}/admin/user/list`,{params:params})};
//平台管理查看解密结果
apis.admnowcode=(params)=>{ return axios.get(`${base.URL}/admin/code2Data`,{params:params})};

//政府管理
//查询用户信息
apis.govuser=(params)=>{ return axios.get(`${base.URL}/gov/allUserApply`,{params:params})};
//审核申请
apis.govcheck=(params1,params2)=>{ return axios.put(`${ base.URL}/gov/${params1}/checkApply/${params2}`)}
//政府删除用户申请
apis.govdelcheck=(params1,params)=>{ return axios.delete(`${base.URL}/gov/${params1}/deleteApply/${params}`)};
//删除数据
apis.govdeldata=(params1,params)=>{ return axios.delete(`${base.URL}/gov/${params1}/deleteData`,{params:params})};
//添加数据
apis.govadddata=(params1,params)=>{ return axios.post(`${base.URL}/gov/${params1}/insertData`,QS.stringify(params))};
//查询数据
apis.govfinddata=(params1,params)=>{ return axios.get(`${base.URL}/gov/${params1}/selectAllData`,{params:params})};
//修改数据
apis.govupdatedata=(params1,params)=>{ return axios.put(`${base.URL}/gov/${params1}/updateData`,QS.stringify(params))};

//浪潮人员
//管理平台模型
apis.getal=(params)=>{ return axios.get(`${base.URL}/lc/al`,{params:params})};
//管理发布计算模型
apis.postal=(params)=>{ return axios.post(`${base.URL}/lc/al`,QS.stringify(params))};
//管理更新计算模型
apis.updateal=(params1,params)=>{ return axios.put(`${base.URL}/lc/al/${params1}`,QS.stringify(params))};
//删除计算模型
apis.deleteal=(params)=>{ return axios.delete(`${base.URL}/lc/al/${params}`)};

export default apis;