<template>
    <div class="header">
        <div class="header-container">
            <div class="header-title">
                <h4>多方安全计算服务平台</h4>
            </div>
            <div class="header-logout">
                <span class="header-role">
                    <i class="el-icon-user usericon"></i>
                    用户角色:
                </span>
                <el-dropdown size="small" 
                    split-button
                    @command="handleCommand">
                    <span class="el-dropdown-link">
                        {{role}}
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            role:''
        }
    },
    methods:{
        logout(){
            sessionStorage.clear();
            this.$router.push('/auth/login');
        },
        handleCommand(command){
            if(command === 'logout'){
                sessionStorage.clear();
                this.$router.push('/auth/login');
                return this.$message({
                    message:'退出成功',
                    type:'success'
                });
            }
        },
        getRole(){
            let that =this;
            const Role=function(role){
                switch (role){
                    case 'user':
                        that.role = '平台用户';
                        break;
                    case 'admin':
                        that.role = '平台管理员';
                        break;
                    case 'gov':
                        that.role = '政府管理员';
                        break;
                    case 'langChao':
                        that.role = '浪潮管理员';
                        break;
                }
            }
            let role = sessionStorage.getItem('role');
            Role(role);
        }
    },
    created(){
        this.getRole();
    }
}
</script>

<style lang="scss" scoped>
    $topheight: 60px;

    .header-container{
        width: 100%;
        height: $topheight;
        background: linear-gradient(to right,#1c8bd2,#0fc6ce);
        // border-bottom: 1px solid #f0f0f0;
        position: absolute;
        top:0;
        left: 0;
        right: 0;
        z-index: 99;
        display: flex;
        align-items: center;
    }
    .header-logout{
        color: #fff;
        .usericon{
            font-size: 24px;
        }
    }
    .header-title{
        padding: 10px;
        color: #fff;
    }
    .header-role{
        font-size: 14px;
        margin: 0 10px;
    }
    .header-logout{
        position: absolute;
        right: 20px;
    }
</style>