import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    useralList:[],
    userinfo:{}
  },
  mutations: {
    getUserList(state,data){
      state.useralList=data;
    },
    getUserInfo(state,data){
      state.useralList.forEach(item=>{
        if(item.id == data){
          state.userinfo = item;
        }
      })
      
    }
  },
  actions: {
  },
  modules: {
  }
})
