<template>
    <div class="side">
        <div class="side-container">
            <el-menu
                class="sidebar-el-menu"
                :default-active="onRoutes"
                background-color="#324157"
                text-color="#bfcbd9"
                active-text-color="#20a0ff"
                unique-opened
                router>
                <template v-for="item in items">
                    <template>
                        <el-menu-item :index="item.path" :key="item.path">
                            <i :class="item.icon"></i>
                            <span slot="title">{{ item.title }}</span>
                        </el-menu-item>
                    </template>
                </template>
            </el-menu>
        </div>
    </div>
</template>

<script>
export default {
    computed:{
        onRoutes(){
            return this.$route.path.replace('/index/','');
        }
    },
    data(){
        return{
            items:[]
        }
    },
    methods:{
        getMenuList(){
            let role = sessionStorage.getItem('role'),
                that=this;
            const ToPath=function(role){
                switch (role){
                    case 'user':
                        that.items=[
                            {
                                icon: 'el-icon-help',
                                path: 'user',
                                title: '数据模型'
                            }
                        ]
                        break;
                    case 'admin':
                        that.items=[
                            {
                                icon: 'el-icon-c-scale-to-original',
                                path: 'admin',
                                title: '用户使用数据'
                            },
                            {
                                icon: 'el-icon-user',
                                path: 'adduser',
                                title: '用户管理'
                            }
                        ]
                        break;
                    case 'gov':
                        that.items=[
                            {
                                icon: 'el-icon-c-scale-to-original',
                                path: 'government',
                                title: '数据管理'
                            },
                            {
                                icon: 'el-icon-user',
                                path: 'govapply',
                                title: '用户管理'
                            }
                        ]
                        break;
                    case 'langChao':
                        that.items=[
                            {
                                icon: 'el-icon-help',
                                path: 'langchao',
                                title: '模型管理'
                            }
                        ]
                        break;
                }
            }
            ToPath(role);
        }
    },
    created(){
        this.getMenuList();
    }
}
</script>

<style lang="scss" scoped>
    $bg:#283443;
    $topH:60px;

    .side-container{
        width: 180px;
        background: $bg;
        position: absolute;
        bottom: 0;
        top:60px;
        left: 0;
    }

</style>